@charset "UTF-8";
:root {
  --clip: left center;
  --size: 0;
  --background-thumb: #2b3769;
  --scrollbar-thumb-bgcolor: #757575;
  --shadow-size: 0px 0px 10px 1px rgba(0, 0, 0, 0.4);
  --stripe-width: 10px;
  --nav-marker-border: #3b8686;
  --sidebar-width: 28.5rem;
  --duration: 0.3s;
  --button-size: 4rem;
  --mob-expanded-width: 320px;
  --mob-collapsed-width: 4rem;
}

.masked-input {
  color: transparent; /* Скрыть текст, пока маска не активна */
}

.masked-input::placeholder {
  color: black; /* Цвет плейсхолдера */
}

.gradient-preloader {
  background: linear-gradient(90deg, #2b3769 -23.739%, #93a9ff 96.778%);
}

@media (min-width: 1440.99px) {
  .underline:hover {
    opacity: 1;
  }
  .underline:hover::before {
    width: 47%;
  }
  .underline:hover::after {
    width: 47%;
  }
  .underline::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    width: 0;
    height: 1px;
    background-color: #000;
    opacity: 0.8;
    transition: width 0.3s ease-in-out;
  }
  .underline::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 0;
    height: 1px;
    background-color: #000;
    opacity: 0.8;
    transition: width 0.3s ease-in-out;
  }
  .underline-w:hover {
    opacity: 1;
  }
  .underline-w:hover::before {
    width: 47%;
  }
  .underline-w:hover::after {
    width: 47%;
  }
  .underline-w::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    width: 0;
    height: 1px;
    background-color: #fff;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline-w::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 0;
    height: 1px;
    background-color: #fff;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
}
.footer {
  max-width: 1920px;
  margin: 0 auto;
  box-sizing: content-box;
}
.footer__body {
  position: relative;
  padding: 70px 50px 120px;
  overflow: hidden;
}
@media screen and (max-width: 1440px) {
  .footer__body {
    padding: 50px 10px;
  }
}
@media screen and (max-width: 375px) {
  .footer__body {
    padding: 50px 10px 10px;
  }
}
@media (min-width: 490.99px) {
  .footer__body {
    background-image: radial-gradient(ellipse at 17% 15%, #171b1c 25%, rgba(23, 27, 28, 0.4039215686) 60%, transparent 100%), radial-gradient(circle at 40% 140%, rgba(238, 238, 238, 0.4156862745) 0%, #2b3769 25%, transparent), radial-gradient(circle at 100% 100%, rgba(238, 238, 238, 0.3019607843) 0%, #2b3769 20%, transparent), radial-gradient(ellipse at 30% 2%, #171b1c 70%, rgb(43, 55, 105) 100%, transparent), radial-gradient(circle at center left, rgba(238, 238, 238, 0) 10%, rgb(4, 5, 10) 50%, transparent);
  }
}
@media screen and (max-width: 490px) {
  .footer__body {
    background-image: radial-gradient(ellipse at bottom center, rgba(43, 55, 105, 0.3568627451), transparent), radial-gradient(ellipse at 10% 19%, #171b1c 35%, rgba(238, 238, 238, 0) 70%, transparent 100%), radial-gradient(circle at 0% 100%, rgba(238, 238, 238, 0.1450980392) 0%, rgba(43, 55, 105, 0.3921568627) 30%, transparent), radial-gradient(circle at 100% 0%, rgba(238, 238, 238, 0.2549019608) 0%, #2b3769 30%, transparent), radial-gradient(ellipse at 30% 2%, #171b1c 0%, #2b3769 100%, transparent);
  }
}
.footer__row {
  position: relative;
  z-index: 9;
}
.footer__header {
  border-bottom: 1.54px solid rgba(188, 188, 188, 0.5568627451);
  padding-bottom: 26px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 60px;
  justify-content: space-between;
}
@media screen and (max-width: 1440px) {
  .footer__header {
    gap: 20px;
  }
}
@media screen and (max-width: 490px) {
  .footer__header {
    gap: 10px;
  }
}
@media screen and (max-width: 490px) {
  .footer__header {
    grid-template-columns: repeat(1, 1fr);
  }
}
.footer__logo {
  display: flex;
  align-items: center;
  margin-left: -5px;
}
.footer__logo a {
  transition: all 0.4s ease 0s;
  white-space: nowrap;
  color: #fff;
  font-size: 19px;
  line-height: 116.666667%; /* 28px/24 */
}
@media screen and (max-width: 690px) {
  .footer__logo a {
    font-size: 15px;
  }
}
.footer__logo a:hover {
  opacity: 0.6;
}
.footer__icons {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
@media screen and (max-width: 490px) {
  .footer__icons {
    justify-content: flex-start;
  }
}
.footer__icons .icon-telegram-fly {
  background: #039be5;
}
.footer__icons .icon-whatsapp {
  background: #4caf50;
}
.footer__icons .icon-vk-brand {
  background: #2b3769;
}
.footer__icons .icon-vk-brand::before {
  font-size: 1.8rem !important;
}
.footer__icons .icon-youtube-lg {
  background: #f03;
}
.footer__icons .icon-youtube-lg::before {
  color: white !important;
}
.footer__icons .icon-dzen::before {
  font-size: 3.299rem !important;
}
.footer__icons ._icon {
  width: 3.3rem;
  height: 3.3rem;
  border-radius: 50%;
}
.footer__icons ._icon::before {
  color: #fff;
  font-size: 2rem;
  font-weight: 400;
}
.footer__icons .footer__link:hover {
  animation: shake 0.5s cubic-bezier(0.68, -0.55, 0.27, 1.55) both;
}
.footer__link {
  color: #fff;
  white-space: nowrap;
}
.footer__link:not(:last-child) {
  margin-right: 10px;
}
.footer__content {
  position: relative;
  z-index: 9;
  padding-top: 25px;
  display: grid;
  justify-content: space-between;
  grid-template-columns: minmax(150px, 33.333%) minmax(170px, 180px) minmax(150px, 33.333%);
  gap: 60px;
  height: 100%;
}
@media (min-width: 1440.98px) and (max-width: 1920px) {
  .footer__content {
    gap: calc(20px + 40 * (100vw - 1440px) / 480);
  }
}
@media screen and (max-width: 1440px) {
  .footer__content {
    gap: 15px;
    column-gap: 30px;
  }
}
@media screen and (max-width: 768px) {
  .footer__content {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (max-width: 425px) {
  .footer__content {
    grid-template-columns: 1fr;
  }
}
.footer__column {
  display: flex;
  gap: 10px;
  flex-direction: column;
  padding-bottom: 20px;
}
@media (min-width: 768.98px) {
  .footer__column:nth-child(3) {
    align-items: flex-end;
  }
}
@media screen and (max-width: 768px) {
  .footer__column:nth-child(1) {
    border-bottom: 1px solid #414141;
  }
  .footer__column:nth-child(2) {
    grid-row: 1/2;
    grid-column: 1/3;
  }
  .footer__column:not(:last-child) {
    border-bottom: 1px solid rgba(65, 65, 65, 0.3568627451);
  }
}
@media screen and (max-width: 425px) {
  .footer__column:nth-child(1) {
    grid-column: 1/2;
    grid-row: 2/3;
  }
  .footer__column:nth-child(2) {
    grid-column: 1/2;
  }
}
.footer__list:not(:last-child) {
  margin-bottom: 5px;
}
@media screen and (max-width: 690px) {
  .footer__list:not(:last-child) {
    margin-bottom: 0;
  }
}
@media screen and (max-width: 768px) {
  .footer__menu {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-content: space-between;
    gap: 30px;
  }
}
.footer__head {
  font-size: 16px;
  color: #8d8d8d;
  text-align: left;
}
@media screen and (max-width: 490px) {
  .footer__head {
    font-size: 15px;
    margin-bottom: 0 !important;
  }
}
.footer__info {
  flex: 1 1 auto;
}
.footer__info li {
  color: #ffffff;
  line-height: 118.75%; /* 19px/16 */
  text-transform: uppercase;
}
.footer__info li:not(:last-child) {
  margin-bottom: 10px;
}
@media screen and (max-width: 690px) {
  .footer__info li {
    font-size: calc(12px + 4 * (100vw - 320px) / 640);
  }
}
@media screen and (max-width: 490px) {
  .footer__info li {
    font-size: 12px;
  }
}
.footer__privacy-policy {
  color: #fff;
  line-height: 118.75%; /* 19px/16 */
}
.footer__item .footer__link {
  line-height: initial;
}
.footer__item .footer__link:hover {
  color: #ffffff;
}
.footer__item:not(:last-child) {
  margin-bottom: 5px;
}
@media (min-width: 768.98px) {
  .footer__contacts {
    flex: 1 1 auto;
  }
}
@media screen and (max-width: 768px) {
  .footer__contacts {
    margin-bottom: 2rem;
  }
}
.footer__phone {
  color: #fff;
  font-size: 16px;
  font-weight: 400;
  text-align: left;
  margin-bottom: 10px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  flex-wrap: wrap;
  line-height: 125%;
}
.footer__phone span {
  white-space: nowrap;
}
.footer__phone li:not(:last-child) {
  margin-bottom: 10px;
}
@media screen and (max-width: 690px) {
  .footer__phone {
    font-size: calc(12px + 4 * (100vw - 320px) / 640);
  }
}
@media screen and (max-width: 490px) {
  .footer__phone {
    font-size: 12px;
  }
}
.footer__mail {
  color: #fff;
  line-height: 118.75%; /* 19px/16 */
  text-align: left;
}
.footer__address {
  color: #fff;
  max-width: 350px;
  line-height: 150.923077%; /* 23/13 */
}
@media (min-width: 768.98px) {
  .footer__address {
    text-align: right;
  }
}

.link-transform {
  height: 100%;
  position: relative;
  display: inline-block;
  transition: all 0.2s ease;
  overflow: hidden;
}
.link-transform span {
  position: relative;
  display: block;
  transform: translateY(0);
}
.link-transform span::before {
  content: attr(data-text);
  font-weight: 400 !important;
  position: absolute;
  top: 100%;
  left: 0;
  transition: transform 0.5s ease;
  transform: translateY(0);
  pointer-events: none;
  user-select: none;
}
@media (min-width: 1440.98px) and (max-width: 1920px) {
  .link-transform span {
    font-size: calc(14px + 2 * (100vw - 1440px) / 480);
  }
}
.link-transform:hover span {
  animation: moveText 0.4s forwards;
}

