  background-color: #ffffff;
  opacity: 0.8;
  transition: width 0.3s ease-in-out;
}
.underline-fl::after, .footer__item .footer__link::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 0;
  height: 1px;
  background-color: #ffffff;
  opacity: 0.8;
  transition: width 0.3s ease-in-out;
}